import React, {Component} from 'react';
import cssClasses from './Grid.module.css';
import Toolbar from '../Toolbar/Toolbar';
import Legend from '../Legend/Legend';
import { UNVISITED,VISITED,VISITING,OBSTRUCTION } from './GRID_CONSTANTS';
export default class Grid extends Component{
    constructor(props){
        super(props);
        this.state={title: 'Dummy Algorithm',
        currentAlgorithm:'',
        currentAlgrithmKey:-1,
        algorithms:['Breadth-First-Search','Depth-First-Search','Dijkstra'],
        mazes:['A','B','C'],
        cellsLoaded:false
    };
    }
    componentDidMount(){
        this.populateDS();
    }
    populateDS=()=>{
        let board=[];
        let cells={};
        let row,x;
        for(let i=0;i<20;i++)
        {
            row=[];
            for(let j=0;j<50;j++)
            {
                x={
                    i:i,
                    j:j,
                   state: UNVISITED,
                   key: i+'-'+j,
                   className: 'cssClasses.unvisited'
                };
                cells[x.key]=x;
                row.push(x);
            }
            board.push(row);
        }
        this.setState({
            ...this.state,
            grid:board,
            cells:cells,
            src:board[0][0],
            dst:board[10][23]
        },()=>{
            this.setState({
                ...this.state,
                cellsLoaded:true,
                cells:cells
            });
            console.log(this.state);
        });
    }
    tdClickHandler=(key)=>{
        console.log(key);        
        if(key===this.state.src.key || key===this.state.dst.key) return;
        this.setState(prevState => {
            //console.log("grid");
            let state = Object.assign({}, prevState);  // creating copy of state variable jasper
            console.log(state.cells[key].state,'sdc',OBSTRUCTION);
                state.cells[key].state=OBSTRUCTION;                   // update the name property, assign a new value                 
                state.cells[key].className=cssClasses.obstruction;
            return {state};
        });
    }
    componentWillReceiveProps(nextProps) {
        console.log('^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^');
        this.setState({title: nextProps.title})
      }

    render()
    {
        return (
            <div>
        <Toolbar algorithms={this.state.algorithms} mazes={this.state.mazes} mazeHandler={this.selectMazeHandler} algorithmHandler={this.selectAlgorithmHandler} />
        <Legend />
        <div className="card card-block">
            <h4 className="card-title"><b>title</b></h4>

            <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
            <div className="flex-row">
            <table align='center'>
                <thead>
                <tr>
                </tr>
                </thead>
                <tbody>
                    {this.state.cellsLoaded?this.state.grid.map((row,index)=>{
                        return (
                            <tr key={index}>
                            {row.map((cell,index)=>{
                                return (
                                <td id={cell.key} key={cell.key} className={cell.className} onMouseDownCapture={this.tdClickHandler.bind(this,cell.key)}  onDrag={this.tdClickHandler.bind(this,cell.key)}>
                                    {this.state.src.key===cell.key?<i className="fas fa-male"></i>:null}
                                    {this.state.dst.key===cell.key?<i className="fa fa-flag"  aria-hidden="true"></i>:null}</td>
                                )
                            })}

                        </tr>
                        );
                    }):<tr><td>Loading</td></tr>}
                </tbody>
            </table>
            <div>cfsd<button className="btn" onClick={this.bfs}>Breadth First Search</button>
                </div>
            </div>
        </div>   
        </div>     
        );
    }


    selectAlgorithmHandler=(key)=>{
        this.setState({
            ...this.state,
            currentAlgorithm:this.state.algorithms[key],
            currentAlgorithmKey:key
        });
        
    }
    selectMazeHandler=(key)=>{
        this.setState({
            ...this.state,
            currentMaze:this.state.mazes[key],
            currentMazeKey:key
        });
    }
    bfs=()=>{
        let board=JSON.parse(JSON.stringify(this.state.cells));
        let src=board[this.state.src.key];
        let dst=board[this.state.dst.key];
        let currentCell,i,j,hashKey;
        let queue=[];
        let visualQueue=[];
        let previous={};
        let result=[];
        let r=[-1,0,1,0]; //x,y x-1,y x+1,y x,y-1 x,y+1
        let c=[0,-1,0,1];
        console.log('src:',src);
        console.log('dst:',dst);
        console.log('%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%')
        queue.push(src);
        var counter=0;
        let updateState=(prevState)=>{
            let state = Object.assign({}, prevState);  // creating copy of state variable jasper
            state.cells[currentCell.key].state=VISITED;                   // update the name property, assign a new value                 
            state.cells[currentCell.key].className=cssClasses.visited;
            return {state};
        }
        let afterUpdate=()=>{
            console.log(currentCell.key);
        }
        while(queue.length!=0)
        {
            currentCell=queue.shift();
            if(currentCell.state===VISITED) continue;
            if(currentCell.key===dst.key)
            {
                visualQueue.push(currentCell.key);
                break;
            }
            currentCell.state=VISITED;
            //currentCell.className=cssClasses.visited;
            visualQueue.push(currentCell.key);
            for(let a=0;a<r.length;a++) //or c.length
            {
                i=currentCell.i+r[a];
                j=currentCell.j+c[a];
                hashKey=i+'-'+j;
                if(board[hashKey] && board[hashKey].state===UNVISITED)
                {
                    queue.push(board[hashKey]);
                    previous[hashKey]=currentCell.i+'-'+currentCell.j;
                }
            }


        }
        console.log('%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%')
        console.log('queue:'+queue.length);
        console.log(currentCell);
        let node=dst.key;
        let path=[];
        while(1)
        {
            if(node)
            {
                path.push(node);
                node=previous[node];
                console.log(node);
                if(node===src.key)
                {
                    path.push(node);
                    break;
                }
            }
            else break;
        }
        var inter=setInterval(()=>{
            if(visualQueue.length===0 && path.length===0) clearInterval(inter);
            else if(visualQueue.length!==0)
            {
                currentCell=board[visualQueue.shift()];
                //this.setState(updateState,afterUpdate);
                document.getElementById(currentCell.key).className=cssClasses.visited;
            }
            else document.getElementById(path.pop()).className=cssClasses.path;
        },50);
        // inter=setInterval(()=>{
        //     if(path.length===0) clearInterval(inter);
        //     else
        //     {
        //         let b=document.getElementById(path.unshift())
        //         if(b) b.className=cssClasses.path
        //     }
        // },100);
    }


}